'use client'

import React from 'react'
import { useRecurringDate } from '@/contexts/recurring-date-context'
import { Button } from '@/components/ui/button'
import { Calendar } from '@/components/ui/calendar'
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover'
import { Label } from '@/components/ui/label'
import { CalendarIcon, X } from 'lucide-react'
import { format } from 'date-fns'
import { cn } from '@/lib/utils'

export function DateRangeSelector() {
  const { state, dispatch } = useRecurringDate()

  const handleStartDateChange = (date: Date | undefined) => {
    dispatch({ type: 'SET_START_DATE', payload: date || null })
  }

  const handleEndDateChange = (date: Date | undefined) => {
    dispatch({ type: 'SET_END_DATE', payload: date || null })
  }

  const clearEndDate = () => {
    dispatch({ type: 'SET_END_DATE', payload: null })
  }

  return (
    <div className="space-y-4">
      {/* Start Date */}
      <div className="space-y-2">
        <Label className="text-sm font-medium">Start Date *</Label>
        <Popover>
          <PopoverTrigger asChild>
            <Button
              variant="outline"
              className={cn(
                "w-full justify-start text-left font-normal",
                !state.startDate && "text-muted-foreground"
              )}
            >
              <CalendarIcon className="mr-2 h-4 w-4" />
              {state.startDate ? format(state.startDate, "PPP") : "Select start date"}
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto p-0" align="start">
            <Calendar
              mode="single"
              selected={state.startDate || undefined}
              onSelect={handleStartDateChange}
              initialFocus
              disabled={(date) => date < new Date(new Date().setHours(0, 0, 0, 0))}
            />
          </PopoverContent>
        </Popover>
      </div>

      {/* End Date */}
      <div className="space-y-2">
        <div className="flex items-center justify-between">
          <Label className="text-sm font-medium">End Date (Optional)</Label>
          {state.endDate && (
            <Button
              variant="ghost"
              size="sm"
              onClick={clearEndDate}
              className="h-auto p-1 text-xs text-gray-500 hover:text-gray-700"
            >
              <X className="h-3 w-3 mr-1" />
              Clear
            </Button>
          )}
        </div>
        <Popover>
          <PopoverTrigger asChild>
            <Button
              variant="outline"
              className={cn(
                "w-full justify-start text-left font-normal",
                !state.endDate && "text-muted-foreground"
              )}
            >
              <CalendarIcon className="mr-2 h-4 w-4" />
              {state.endDate ? format(state.endDate, "PPP") : "Select end date (optional)"}
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto p-0" align="start">
            <Calendar
              mode="single"
              selected={state.endDate || undefined}
              onSelect={handleEndDateChange}
              initialFocus
              disabled={(date) => {
                const today = new Date(new Date().setHours(0, 0, 0, 0))
                const startDate = state.startDate || today
                return date < today || date <= startDate
              }}
            />
          </PopoverContent>
        </Popover>
        <p className="text-xs text-gray-500">
          If no end date is specified, the preview will show dates for the next year.
        </p>
      </div>

      {/* Date Range Summary */}
      {state.startDate && (
        <div className="p-3 bg-blue-50 rounded-lg border border-blue-200">
          <div className="text-sm">
            <p className="font-medium text-blue-900">Selected Range:</p>
            <p className="text-blue-700">
              From: {format(state.startDate, "PPP")}
            </p>
            {state.endDate && (
              <p className="text-blue-700">
                To: {format(state.endDate, "PPP")}
              </p>
            )}
            {!state.endDate && (
              <p className="text-blue-600 text-xs">
                No end date - will continue indefinitely
              </p>
            )}
          </div>
        </div>
      )}
    </div>
  )
}
