'use client'

import React, { createContext, useContext, useReducer, ReactNode } from 'react'

// Types for the recurring date picker
export type RecurrenceType = 'daily' | 'weekly' | 'monthly' | 'yearly'

export type WeekDay = 'monday' | 'tuesday' | 'wednesday' | 'thursday' | 'friday' | 'saturday' | 'sunday'

export type MonthlyPattern = 'date' | 'day' // e.g., "15th of every month" vs "second Tuesday of every month"

export interface RecurringDateState {
  recurrenceType: RecurrenceType
  interval: number // Every X days/weeks/months/years
  startDate: Date | null
  endDate: Date | null
  selectedWeekDays: WeekDay[]
  monthlyPattern: MonthlyPattern
  monthlyWeekDay: WeekDay
  monthlyWeekNumber: number // 1st, 2nd, 3rd, 4th, last (-1)
  previewDates: Date[]
}

type RecurringDateAction =
  | { type: 'SET_RECURRENCE_TYPE'; payload: RecurrenceType }
  | { type: 'SET_INTERVAL'; payload: number }
  | { type: 'SET_START_DATE'; payload: Date | null }
  | { type: 'SET_END_DATE'; payload: Date | null }
  | { type: 'TOGGLE_WEEK_DAY'; payload: WeekDay }
  | { type: 'SET_MONTHLY_PATTERN'; payload: MonthlyPattern }
  | { type: 'SET_MONTHLY_WEEK_DAY'; payload: WeekDay }
  | { type: 'SET_MONTHLY_WEEK_NUMBER'; payload: number }
  | { type: 'UPDATE_PREVIEW_DATES'; payload: Date[] }
  | { type: 'RESET' }

const initialState: RecurringDateState = {
  recurrenceType: 'daily',
  interval: 1,
  startDate: null,
  endDate: null,
  selectedWeekDays: [],
  monthlyPattern: 'date',
  monthlyWeekDay: 'monday',
  monthlyWeekNumber: 1,
  previewDates: []
}

function recurringDateReducer(state: RecurringDateState, action: RecurringDateAction): RecurringDateState {
  switch (action.type) {
    case 'SET_RECURRENCE_TYPE':
      return { ...state, recurrenceType: action.payload }
    case 'SET_INTERVAL':
      return { ...state, interval: Math.max(1, action.payload) }
    case 'SET_START_DATE':
      return { ...state, startDate: action.payload }
    case 'SET_END_DATE':
      return { ...state, endDate: action.payload }
    case 'TOGGLE_WEEK_DAY':
      const weekDay = action.payload
      const isSelected = state.selectedWeekDays.includes(weekDay)
      const selectedWeekDays = isSelected
        ? state.selectedWeekDays.filter(day => day !== weekDay)
        : [...state.selectedWeekDays, weekDay]
      return { ...state, selectedWeekDays }
    case 'SET_MONTHLY_PATTERN':
      return { ...state, monthlyPattern: action.payload }
    case 'SET_MONTHLY_WEEK_DAY':
      return { ...state, monthlyWeekDay: action.payload }
    case 'SET_MONTHLY_WEEK_NUMBER':
      return { ...state, monthlyWeekNumber: action.payload }
    case 'UPDATE_PREVIEW_DATES':
      return { ...state, previewDates: action.payload }
    case 'RESET':
      return initialState
    default:
      return state
  }
}

interface RecurringDateContextType {
  state: RecurringDateState
  dispatch: React.Dispatch<RecurringDateAction>
  generatePreviewDates: () => void
}

const RecurringDateContext = createContext<RecurringDateContextType | undefined>(undefined)

export function RecurringDateProvider({ children }: { children: ReactNode }) {
  const [state, dispatch] = useReducer(recurringDateReducer, initialState)

  const generatePreviewDates = () => {
    if (!state.startDate) return

    const dates: Date[] = []
    const startDate = new Date(state.startDate)
    const endDate = state.endDate ? new Date(state.endDate) : new Date(startDate.getTime() + 365 * 24 * 60 * 60 * 1000) // Default to 1 year
    const maxDates = 50 // Limit preview to 50 dates

    let currentDate = new Date(startDate)
    let count = 0

    while (currentDate <= endDate && count < maxDates) {
      switch (state.recurrenceType) {
        case 'daily':
          dates.push(new Date(currentDate))
          currentDate.setDate(currentDate.getDate() + state.interval)
          break

        case 'weekly':
          if (state.selectedWeekDays.length === 0) {
            dates.push(new Date(currentDate))
            currentDate.setDate(currentDate.getDate() + 7 * state.interval)
          } else {
            // Handle specific days of the week
            const weekStart = new Date(currentDate)
            weekStart.setDate(weekStart.getDate() - weekStart.getDay() + 1) // Start of week (Monday)
            
            for (const weekDay of state.selectedWeekDays) {
              const dayIndex = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'].indexOf(weekDay)
              const targetDate = new Date(weekStart)
              targetDate.setDate(weekStart.getDate() + dayIndex)
              
              if (targetDate >= startDate && targetDate <= endDate) {
                dates.push(new Date(targetDate))
              }
            }
            currentDate.setDate(currentDate.getDate() + 7 * state.interval)
          }
          break

        case 'monthly':
          if (state.monthlyPattern === 'date') {
            dates.push(new Date(currentDate))
            currentDate.setMonth(currentDate.getMonth() + state.interval)
          } else {
            // Handle "nth weekday of month" pattern
            const targetDate = getNthWeekdayOfMonth(
              currentDate.getFullYear(),
              currentDate.getMonth(),
              state.monthlyWeekDay,
              state.monthlyWeekNumber
            )
            if (targetDate && targetDate >= startDate && targetDate <= endDate) {
              dates.push(targetDate)
            }
            currentDate.setMonth(currentDate.getMonth() + state.interval)
          }
          break

        case 'yearly':
          dates.push(new Date(currentDate))
          currentDate.setFullYear(currentDate.getFullYear() + state.interval)
          break
      }
      count++
    }

    dispatch({ type: 'UPDATE_PREVIEW_DATES', payload: dates.sort((a, b) => a.getTime() - b.getTime()) })
  }

  return (
    <RecurringDateContext.Provider value={{ state, dispatch, generatePreviewDates }}>
      {children}
    </RecurringDateContext.Provider>
  )
}

export function useRecurringDate() {
  const context = useContext(RecurringDateContext)
  if (context === undefined) {
    throw new Error('useRecurringDate must be used within a RecurringDateProvider')
  }
  return context
}

// Helper function to get nth weekday of month
function getNthWeekdayOfMonth(year: number, month: number, weekDay: WeekDay, weekNumber: number): Date | null {
  const weekDayIndex = ['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'].indexOf(weekDay)
  const firstDay = new Date(year, month, 1)
  const firstWeekDay = firstDay.getDay()
  
  let targetDate: number
  
  if (weekNumber === -1) {
    // Last occurrence of the weekday in the month
    const lastDay = new Date(year, month + 1, 0).getDate()
    const lastDayOfMonth = new Date(year, month, lastDay)
    const lastWeekDay = lastDayOfMonth.getDay()
    
    const daysBack = (lastWeekDay - weekDayIndex + 7) % 7
    targetDate = lastDay - daysBack
  } else {
    // Nth occurrence of the weekday
    const daysToAdd = (weekDayIndex - firstWeekDay + 7) % 7
    targetDate = 1 + daysToAdd + (weekNumber - 1) * 7
  }
  
  // Check if the calculated date is valid for the month
  const calculatedDate = new Date(year, month, targetDate)
  if (calculatedDate.getMonth() !== month) {
    return null
  }
  
  return calculatedDate
}
