'use client'

import React, { useEffect } from 'react'
import { useRecurringDate } from '@/contexts/recurring-date-context'
import { RecurrenceOptions } from './recurring-date-picker/recurrence-options'
import { CustomizationFeatures } from './recurring-date-picker/customization-features'
import { DateRangeSelector } from './recurring-date-picker/date-range-selector'
import { MiniCalendarPreview } from './recurring-date-picker/mini-calendar-preview'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Separator } from '@/components/ui/separator'
import { Button } from '@/components/ui/button'

export function RecurringDatePicker() {
  const { state, dispatch, generatePreviewDates } = useRecurringDate()

  // Generate preview dates whenever relevant state changes
  useEffect(() => {
    generatePreviewDates()
  }, [
    state.recurrenceType,
    state.interval,
    state.startDate,
    state.endDate,
    state.selectedWeekDays,
    state.monthlyPattern,
    state.monthlyWeekDay,
    state.monthlyWeekNumber
  ])

  const handleReset = () => {
    dispatch({ type: 'RESET' })
  }

  const getRecurrenceDescription = () => {
    if (!state.startDate) return 'Please select a start date'

    const startDateStr = state.startDate.toLocaleDateString()
    const endDateStr = state.endDate ? ` until ${state.endDate.toLocaleDateString()}` : ''

    switch (state.recurrenceType) {
      case 'daily':
        return `Every ${state.interval === 1 ? 'day' : `${state.interval} days`} starting ${startDateStr}${endDateStr}`
      
      case 'weekly':
        if (state.selectedWeekDays.length === 0) {
          return `Every ${state.interval === 1 ? 'week' : `${state.interval} weeks`} starting ${startDateStr}${endDateStr}`
        } else {
          const days = state.selectedWeekDays.map(day => day.charAt(0).toUpperCase() + day.slice(1)).join(', ')
          return `Every ${state.interval === 1 ? 'week' : `${state.interval} weeks`} on ${days} starting ${startDateStr}${endDateStr}`
        }
      
      case 'monthly':
        if (state.monthlyPattern === 'date') {
          return `Every ${state.interval === 1 ? 'month' : `${state.interval} months`} on day ${state.startDate.getDate()} starting ${startDateStr}${endDateStr}`
        } else {
          const weekNumber = state.monthlyWeekNumber === -1 ? 'last' : 
            ['first', 'second', 'third', 'fourth'][state.monthlyWeekNumber - 1]
          const weekDay = state.monthlyWeekDay.charAt(0).toUpperCase() + state.monthlyWeekDay.slice(1)
          return `Every ${state.interval === 1 ? 'month' : `${state.interval} months`} on the ${weekNumber} ${weekDay} starting ${startDateStr}${endDateStr}`
        }
      
      case 'yearly':
        return `Every ${state.interval === 1 ? 'year' : `${state.interval} years`} starting ${startDateStr}${endDateStr}`
      
      default:
        return ''
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-semibold text-gray-900">Recurring Date Picker</h2>
          <p className="text-sm text-gray-600 mt-1">Configure your recurring schedule</p>
        </div>
        <Button variant="outline" onClick={handleReset}>
          Reset
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Configuration Panel */}
        <div className="space-y-6">
          {/* Date Range Selection */}
          <Card>
            <CardHeader>
              <CardTitle>Date Range</CardTitle>
              <CardDescription>Select the start and optional end date for your recurring schedule</CardDescription>
            </CardHeader>
            <CardContent>
              <DateRangeSelector />
            </CardContent>
          </Card>

          {/* Recurrence Options */}
          <Card>
            <CardHeader>
              <CardTitle>Recurrence Pattern</CardTitle>
              <CardDescription>Choose how often the event should repeat</CardDescription>
            </CardHeader>
            <CardContent>
              <RecurrenceOptions />
            </CardContent>
          </Card>

          {/* Customization Features */}
          <Card>
            <CardHeader>
              <CardTitle>Customization</CardTitle>
              <CardDescription>Fine-tune your recurring schedule</CardDescription>
            </CardHeader>
            <CardContent>
              <CustomizationFeatures />
            </CardContent>
          </Card>
        </div>

        {/* Preview Panel */}
        <div className="space-y-6">
          {/* Summary */}
          <Card>
            <CardHeader>
              <CardTitle>Schedule Summary</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="p-4 bg-gray-50 rounded-lg">
                <p className="text-sm text-gray-700">
                  {getRecurrenceDescription()}
                </p>
                {state.previewDates.length > 0 && (
                  <p className="text-xs text-gray-500 mt-2">
                    {state.previewDates.length} occurrence{state.previewDates.length !== 1 ? 's' : ''} shown
                  </p>
                )}
              </div>
            </CardContent>
          </Card>

          {/* Mini Calendar Preview */}
          <Card>
            <CardHeader>
              <CardTitle>Calendar Preview</CardTitle>
              <CardDescription>Visual representation of your recurring dates</CardDescription>
            </CardHeader>
            <CardContent>
              <MiniCalendarPreview />
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
