'use client'

import React from 'react'
import { useRecurringDate, WeekDay, MonthlyPattern } from '@/contexts/recurring-date-context'
import { Button } from '@/components/ui/button'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'

const weekDays: { value: WeekDay; label: string; short: string }[] = [
  { value: 'monday', label: 'Monday', short: 'Mon' },
  { value: 'tuesday', label: 'Tuesday', short: 'Tue' },
  { value: 'wednesday', label: 'Wednesday', short: 'Wed' },
  { value: 'thursday', label: 'Thursday', short: 'Thu' },
  { value: 'friday', label: 'Friday', short: 'Fri' },
  { value: 'saturday', label: 'Saturday', short: 'Sat' },
  { value: 'sunday', label: 'Sunday', short: 'Sun' }
]

const weekNumbers = [
  { value: 1, label: 'First' },
  { value: 2, label: 'Second' },
  { value: 3, label: 'Third' },
  { value: 4, label: 'Fourth' },
  { value: -1, label: 'Last' }
]

export function CustomizationFeatures() {
  const { state, dispatch } = useRecurringDate()

  const handleWeekDayToggle = (weekDay: WeekDay) => {
    dispatch({ type: 'TOGGLE_WEEK_DAY', payload: weekDay })
  }

  const handleMonthlyPatternChange = (pattern: MonthlyPattern) => {
    dispatch({ type: 'SET_MONTHLY_PATTERN', payload: pattern })
  }

  const handleMonthlyWeekDayChange = (weekDay: WeekDay) => {
    dispatch({ type: 'SET_MONTHLY_WEEK_DAY', payload: weekDay })
  }

  const handleMonthlyWeekNumberChange = (weekNumber: string) => {
    dispatch({ type: 'SET_MONTHLY_WEEK_NUMBER', payload: parseInt(weekNumber) })
  }

  // Show different customization options based on recurrence type
  const renderCustomizationOptions = () => {
    switch (state.recurrenceType) {
      case 'weekly':
        return (
          <div className="space-y-3">
            <Label className="text-sm font-medium">Select specific days of the week:</Label>
            <div className="grid grid-cols-7 gap-1">
              {weekDays.map((day) => (
                <Button
                  key={day.value}
                  variant={state.selectedWeekDays.includes(day.value) ? 'default' : 'outline'}
                  size="sm"
                  className="h-10 text-xs"
                  onClick={() => handleWeekDayToggle(day.value)}
                >
                  {day.short}
                </Button>
              ))}
            </div>
            {state.selectedWeekDays.length === 0 && (
              <p className="text-xs text-gray-500">
                No specific days selected. Will repeat on the same day of the week as the start date.
              </p>
            )}
          </div>
        )

      case 'monthly':
        return (
          <div className="space-y-4">
            <Label className="text-sm font-medium">Monthly pattern:</Label>
            <RadioGroup
              value={state.monthlyPattern}
              onValueChange={(value) => handleMonthlyPatternChange(value as MonthlyPattern)}
            >
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="date" id="monthly-date" />
                <Label htmlFor="monthly-date" className="text-sm">
                  On the same date each month
                </Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="day" id="monthly-day" />
                <Label htmlFor="monthly-day" className="text-sm">
                  On a specific day of the week
                </Label>
              </div>
            </RadioGroup>

            {state.monthlyPattern === 'day' && (
              <div className="ml-6 space-y-3">
                <div className="flex items-center space-x-2">
                  <Label className="text-sm">The</Label>
                  <Select
                    value={state.monthlyWeekNumber.toString()}
                    onValueChange={handleMonthlyWeekNumberChange}
                  >
                    <SelectTrigger className="w-24">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {weekNumbers.map((week) => (
                        <SelectItem key={week.value} value={week.value.toString()}>
                          {week.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  <Select
                    value={state.monthlyWeekDay}
                    onValueChange={(value) => handleMonthlyWeekDayChange(value as WeekDay)}
                  >
                    <SelectTrigger className="w-32">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {weekDays.map((day) => (
                        <SelectItem key={day.value} value={day.value}>
                          {day.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  <Label className="text-sm">of every month</Label>
                </div>
                <p className="text-xs text-gray-500 ml-0">
                  Example: "The second Tuesday of every month"
                </p>
              </div>
            )}
          </div>
        )

      case 'daily':
        return (
          <div className="text-sm text-gray-500">
            <p>Daily recurrence will repeat every day based on your interval setting.</p>
          </div>
        )

      case 'yearly':
        return (
          <div className="text-sm text-gray-500">
            <p>Yearly recurrence will repeat on the same date each year based on your interval setting.</p>
          </div>
        )

      default:
        return null
    }
  }

  return (
    <div className="space-y-4">
      {renderCustomizationOptions()}
    </div>
  )
}
