'use client'

import React, { useState } from 'react'
import { useRecurringDate } from '@/contexts/recurring-date-context'
import { Button } from '@/components/ui/button'
import { ChevronLeft, ChevronRight } from 'lucide-react'
import { format, startOfMonth, endOfMonth, eachDayOfInterval, isSameDay, isSameMonth, addMonths, subMonths } from 'date-fns'
import { cn } from '@/lib/utils'

export function MiniCalendarPreview() {
  const { state } = useRecurringDate()
  const [currentMonth, setCurrentMonth] = useState(new Date())

  const monthStart = startOfMonth(currentMonth)
  const monthEnd = endOfMonth(currentMonth)
  const monthDays = eachDayOfInterval({ start: monthStart, end: monthEnd })

  // Get the first day of the week (0 = Sunday, 1 = Monday, etc.)
  const firstDayOfMonth = monthStart.getDay()
  const startDate = new Date(monthStart)
  startDate.setDate(startDate.getDate() - firstDayOfMonth)

  // Generate calendar grid (6 weeks x 7 days = 42 days)
  const calendarDays = []
  for (let i = 0; i < 42; i++) {
    const day = new Date(startDate)
    day.setDate(startDate.getDate() + i)
    calendarDays.push(day)
  }

  const isRecurringDate = (date: Date) => {
    return state.previewDates.some(previewDate => isSameDay(previewDate, date))
  }

  const isStartDate = (date: Date) => {
    return state.startDate && isSameDay(date, state.startDate)
  }

  const isEndDate = (date: Date) => {
    return state.endDate && isSameDay(date, state.endDate)
  }

  const navigateMonth = (direction: 'prev' | 'next') => {
    setCurrentMonth(prev => direction === 'prev' ? subMonths(prev, 1) : addMonths(prev, 1))
  }

  const weekDays = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']

  return (
    <div className="space-y-4">
      {/* Calendar Header */}
      <div className="flex items-center justify-between">
        <Button
          variant="outline"
          size="sm"
          onClick={() => navigateMonth('prev')}
        >
          <ChevronLeft className="h-4 w-4" />
        </Button>
        <h3 className="font-semibold text-lg">
          {format(currentMonth, 'MMMM yyyy')}
        </h3>
        <Button
          variant="outline"
          size="sm"
          onClick={() => navigateMonth('next')}
        >
          <ChevronRight className="h-4 w-4" />
        </Button>
      </div>

      {/* Calendar Grid */}
      <div className="border rounded-lg overflow-hidden">
        {/* Week day headers */}
        <div className="grid grid-cols-7 bg-gray-50">
          {weekDays.map((day) => (
            <div key={day} className="p-2 text-center text-xs font-medium text-gray-600 border-r last:border-r-0">
              {day}
            </div>
          ))}
        </div>

        {/* Calendar days */}
        <div className="grid grid-cols-7">
          {calendarDays.map((day, index) => {
            const isCurrentMonth = isSameMonth(day, currentMonth)
            const isRecurring = isRecurringDate(day)
            const isStart = isStartDate(day)
            const isEnd = isEndDate(day)
            const isToday = isSameDay(day, new Date())

            return (
              <div
                key={index}
                className={cn(
                  "relative p-2 h-10 border-r border-b last:border-r-0 flex items-center justify-center text-sm",
                  !isCurrentMonth && "text-gray-300 bg-gray-50",
                  isCurrentMonth && "text-gray-900",
                  isToday && "bg-blue-100 font-semibold",
                  isRecurring && "bg-green-100 text-green-800 font-medium",
                  isStart && "bg-blue-500 text-white font-bold",
                  isEnd && "bg-red-500 text-white font-bold"
                )}
              >
                {day.getDate()}
                
                {/* Indicators */}
                {isStart && (
                  <div className="absolute -top-1 -right-1 w-2 h-2 bg-blue-600 rounded-full"></div>
                )}
                {isEnd && (
                  <div className="absolute -top-1 -left-1 w-2 h-2 bg-red-600 rounded-full"></div>
                )}
                {isRecurring && !isStart && !isEnd && (
                  <div className="absolute -bottom-1 left-1/2 transform -translate-x-1/2 w-1 h-1 bg-green-600 rounded-full"></div>
                )}
              </div>
            )
          })}
        </div>
      </div>

      {/* Legend */}
      <div className="flex flex-wrap gap-4 text-xs">
        <div className="flex items-center gap-1">
          <div className="w-3 h-3 bg-blue-500 rounded"></div>
          <span>Start Date</span>
        </div>
        <div className="flex items-center gap-1">
          <div className="w-3 h-3 bg-red-500 rounded"></div>
          <span>End Date</span>
        </div>
        <div className="flex items-center gap-1">
          <div className="w-3 h-3 bg-green-100 border border-green-300 rounded"></div>
          <span>Recurring Date</span>
        </div>
        <div className="flex items-center gap-1">
          <div className="w-3 h-3 bg-blue-100 border border-blue-300 rounded"></div>
          <span>Today</span>
        </div>
      </div>

      {/* Preview List */}
      {state.previewDates.length > 0 && (
        <div className="space-y-2">
          <h4 className="font-medium text-sm">Upcoming Dates:</h4>
          <div className="max-h-32 overflow-y-auto space-y-1">
            {state.previewDates.slice(0, 10).map((date, index) => (
              <div key={index} className="text-xs p-2 bg-gray-50 rounded flex justify-between">
                <span>{format(date, 'PPP')}</span>
                <span className="text-gray-500">{format(date, 'EEEE')}</span>
              </div>
            ))}
            {state.previewDates.length > 10 && (
              <div className="text-xs text-gray-500 text-center p-1">
                ... and {state.previewDates.length - 10} more dates
              </div>
            )}
          </div>
        </div>
      )}

      {state.previewDates.length === 0 && state.startDate && (
        <div className="text-center text-sm text-gray-500 p-4">
          No recurring dates to preview. Please check your configuration.
        </div>
      )}
    </div>
  )
}
