'use client'

import React from 'react'
import { useRecurringDate, RecurrenceType } from '@/contexts/recurring-date-context'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'

const recurrenceTypes: { value: RecurrenceType; label: string; description: string }[] = [
  { value: 'daily', label: 'Daily', description: 'Repeat every day' },
  { value: 'weekly', label: 'Weekly', description: 'Repeat every week' },
  { value: 'monthly', label: 'Monthly', description: 'Repeat every month' },
  { value: 'yearly', label: 'Yearly', description: 'Repeat every year' }
]

export function RecurrenceOptions() {
  const { state, dispatch } = useRecurringDate()

  const handleRecurrenceTypeChange = (type: RecurrenceType) => {
    dispatch({ type: 'SET_RECURRENCE_TYPE', payload: type })
  }

  const handleIntervalChange = (value: string) => {
    const interval = parseInt(value) || 1
    dispatch({ type: 'SET_INTERVAL', payload: interval })
  }

  const getIntervalLabel = () => {
    switch (state.recurrenceType) {
      case 'daily': return state.interval === 1 ? 'day' : 'days'
      case 'weekly': return state.interval === 1 ? 'week' : 'weeks'
      case 'monthly': return state.interval === 1 ? 'month' : 'months'
      case 'yearly': return state.interval === 1 ? 'year' : 'years'
      default: return 'intervals'
    }
  }

  return (
    <div className="space-y-4">
      {/* Recurrence Type Selection */}
      <div className="grid grid-cols-2 gap-2">
        {recurrenceTypes.map((type) => (
          <Button
            key={type.value}
            variant={state.recurrenceType === type.value ? 'default' : 'outline'}
            className="h-auto p-3 flex flex-col items-start"
            onClick={() => handleRecurrenceTypeChange(type.value)}
          >
            <span className="font-medium">{type.label}</span>
            <span className="text-xs opacity-70 text-left">{type.description}</span>
          </Button>
        ))}
      </div>

      {/* Interval Selection */}
      <div className="flex items-center space-x-2">
        <Label htmlFor="interval" className="text-sm font-medium">
          Every
        </Label>
        <Input
          id="interval"
          type="number"
          min="1"
          max="999"
          value={state.interval}
          onChange={(e) => handleIntervalChange(e.target.value)}
          className="w-20"
        />
        <span className="text-sm text-gray-600">{getIntervalLabel()}</span>
      </div>
    </div>
  )
}
